import React, { useEffect } from "react";
import emailjs from 'emailjs-com';
import AOS from "aos";
import "aos/dist/aos.css";
import Swal from 'sweetalert2';
import {
  Box,
  Container,
  Grid,
  Typography,
  TextField,
  Button,
} from "@mui/material";
import "./contact.css";

export default function Contact() {
  useEffect(() => {
    AOS.init({ duration: 1000 });
    window.scrollTo(0, 0);
    emailjs.init('P1jlrFA2eFxgkYfq_');
  }, []);

  const handleSubmit = (event) => {
    event.preventDefault();
    
    emailjs.sendForm('service_4sarhv9', 'template_ynu7qba', event.target)
      .then((result) => {
        console.log('Success:', result.text);
        Swal.fire({
          title: 'Success!',
          text: 'Message sent successfully!',
          icon: 'success',
          confirmButtonText: 'OK'
        });
        event.target.reset(); 
      }, (error) => {
        console.error('Error:', error.text);
        Swal.fire({
          title: 'Error!',
          text: 'Failed to send message.',
          icon: 'error',
          confirmButtonText: 'OK'
        });
      });
  };

  return (
    <div className="contact-container">
      <Box sx={{ py: 8 }}>
        <Container>
          <Typography
            variant="h4"
            sx={{ textAlign: "center", mb: 4 }}
            data-aos="fade-up"
          >
            <p className="Title">
              Contact <span className="innerTitle">Us</span>
            </p>
          </Typography>
          <Grid container spacing={4}>
            <Grid item xs={12} md={6} data-aos="fade-right">
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  maxWidth: 600,
                  margin: "0 auto",
                  padding: 2,
                  backgroundColor: "#ffffff",
                  borderRadius: 1,
                  boxShadow: 2,
                }}
              >
                <Typography variant="h6" sx={{ mb: 2 }}>
                  Hy cho chng ti  kin ca bn
                </Typography>
                <Typography variant="body1" sx={{ mb: 4 }}>
                  Hy in thng tin vo form bn di v chng ti s lin h
                  vi bn sm nht c th.
                </Typography>
                <form noValidate autoComplete="off" onSubmit={handleSubmit} style={{ width: "100%" }}>
                  <TextField
                    fullWidth
                    label="Name"
                    variant="outlined"
                    margin="normal"
                    required
                    name="name" 
                  />
                  <TextField
                    fullWidth
                    label="Email"
                    variant="outlined"
                    margin="normal"
                    required
                    name="email" 
                  />
                  <TextField
                    fullWidth
                    label="Message"
                    variant="outlined"
                    margin="normal"
                    multiline
                    rows={4}
                    required
                    name="message" 
                  />
                  <Button
                    className="submit_buy"
                    variant="contained"
                    sx={{ mt: 2, backgroundColor: "#dd3431" }}
                    type="submit"
                  >
                    Gi Tin Nhn
                  </Button>
                </form>
              </Box>
            </Grid>
            <Grid item xs={12} md={6} data-aos="fade-left">
              <Typography sx={{ mb: 2 }} variant="h6" className="contact-info-title">
                S lin h{" "}
              </Typography>
              <Typography sx={{ mb: 2 }} variant="body1" className="contact-info">
                <strong>Email:</strong> test@company.com
              </Typography>
              <Typography sx={{ mb: 2 }} variant="body1" className="contact-info">
                <strong>Phone:</strong> +123 456 7890
              </Typography>
              <Typography sx={{ mb: 2 }} variant="body1" className="contact-info">
                <strong>Address:</strong> 123 Street, City, Country
              </Typography>
              <Typography sx={{ mb: 2 }} variant="body2" className="contact-note">
                Chng ti sn sng tr li cc cu hi ca bn. Hy lin h
                chng ti v chng ti s tr li sm nht c th.
              </Typography>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </div>
  );
}
